.App {

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.app-container{
max-width: 1800px;
margin-left:auto;
margin-right:auto;
padding: 0 40px;
}



.header{
padding-top: 20px;
}
.header-row{
display: flex;
align-items: center;
justify-content: space-between;
}
.header-sec-row{
  display: flex;
  align-items: center;
}
.header-logo{
  cursor: pointer;
  max-width: 250px;
}

.delivery{
