.popup{
position: fixed;
width:100%;
height: 100%;
background: rgba(0,0,0,0.7);
top: 0;
left: 0;
z-index: 50;
}
.popup.passive{
display: none;
opacity: 0;
}
.popup{
display: block;
opacity: 1;
}
.popup-body{
min-height: 100%;
display: flex;
justify-content: center;
align-items: center;
padding: 30px 10px;
}
.popup-content{
background-color: #FFFFFF;
max-width: 800px;
padding: 20px 30px;
border-radius: 20px;
}
.popup-row{
  display: flex;
  align-items: center;
  justify-content: space-between;
 
}
.popup-logo{

}
.popup-title{
  padding-left: 10px;
  font-size: 32px;
  font-family: 'Comfortaa';
  font-weight: 700;
}
.popup-title-row{
  
}
.popup-cross{
  max-width: 30px;
  cursor: pointer;
  scale: 1;
  transition: 0.5s ease all 0s;
  padding-left: 20px;
}
.popup-cross:hover{
  scale: 1.1;
  transition: 0.5s ease all 0s;
}
.popup-main-title{
  display: flex;
  align-items: center;
}
.popup-cities-row{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-top: 40px;
  margin-bottom: 60px;
}
.popup-city-item{
  flex: 1 1 33.333%;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 14px;
  font-family: 'Comfortaa';
  font-weight: 900;
  cursor: pointer;
  transition: 0.3s ease all 0s;
}
.popup-city-item:hover{
  color:#ff6900;
  transition: 0.2s ease all 0s;
}
.popup-input{
  padding: 10px 5px;
  font-family: 'Comfortaa';
  font-weight: 700;
  max-width: 250px;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #a6a6a6;
  margin-top: 10px;
}
.popup-input:focus{
  outline: none;
  border: 1px solid #ff6900;
}
@media(max-width:444px){
  .popup-city-item{
    flex: 1 1 50%;
  }
}
@media(max-width:434px){
  .popup-logo{
    
  }
  .popup-title{
    display: none;
  }
  .popup-content{
    width: 100%;
  }
}
